import { useState, useEffect } from 'react';
import axios from 'axios';
export default function ({ handleBankCode }) {
	const [codes, setCodes] = useState([]);
	useEffect(() => {
		var config = {
			method: 'get',
			// https://u2-rest-dev.azurewebsites.net/api/Campaign/challengesubmit
			url: process.env.REACT_APP_U2_DB_HOST + '/common/bankcode',
			headers: {
				// Authorization: 'Bearer ' + localStorage.getItem('token'),
				'Content-Type': 'application/json',
			},
			// data: data,
		};
		axios(config)
			.then((response) => {
				const banks = response.data;
				setCodes(banks);
			})
			.catch((err) => {
				console.log(err);
			});
	}, []);
	return (
		<select
			className="banks_select"
			name="account_bank_id"
			onChange={(e) => {
				handleBankCode(e.target.value);
			}}
		>
			<option value=""> </option>
			{codes.map((el) => {
				return (
					<option value={`${el.code},${el.bankName}`}>{el.bankName}</option>
				);
			})}
			{/* <option value=""> </option>
			<option value="35"></option>
			<option value="29"></option>
			<option value="7"></option>
			<option value="5"></option>
			<option value="15"></option>
			<option value="17"></option>
			<option value="25"></option>
			<option value="47"></option>
			<option value="27"></option>
			<option value="3"></option>
			<option value="41"></option>
			<option value="37"></option>
			<option value="11"></option>
			<option value="36"></option>
			<option value="60"></option>
			<option value="39"></option>
			<option value="9"></option>
			<option value="19"></option>
			<option value="56"></option>
			<option value="33"></option>
			<option value="31"></option>
			<option value="68"></option>
			<option value="67"></option>
			<option value="59"></option>
			<option value="23"></option>
			<option value="45">HSBC</option>
			<option value="21">SC</option> */}
		</select>
	);
}
